html, body {
    font-family: 'Roboto', sans-serif;
    height: 100%;
    padding: 0;
    margin: 0;
    scroll-snap-type: y proximity;

    background-color: #efefef;
    scroll-behavior: smooth;

    font-size: 18px;
}

#header {
    background-color: #4db2f8;
    background-image: url("./imgs/gyerek.png");
    background-position: right;
    background-repeat: no-repeat;
    background-size: auto 100%;

    h1, h2, a {
    color: white;
    text-decoration: none;
    margin: 0;
    }

    padding-left: 0.5rem;
    display: flex;
    align-items: center;

    h1 {
        font-weight: 900;
        font-size: 2.15rem;

        margin-bottom: 0;
    }

    h2 {
        font-size: 1.25rem;
    }

    .menu {
        display: none;
        justify-content: space-evenly;
        align-items: center;
        flex-basis: 80%;

        padding: 0;
        margin: 0;

        list-style-type: none;
        font-size: 1.5rem;

        img {
            width: 48px;
        }

        li:last-child {
            background-color: white;
            border: 2px solid #4db2f8;
            padding: 0.2rem;
            a {color: orange;}
        }
    }
}

.page {
    height: 100vh;
    max-height: 100vh;
    scroll-snap-align: start;
}

.splash {
    display: grid;
    grid-template-rows: 10% 55% 35%;
    grid-template-columns: 100%;
}

#main {
    display: block;
    height: 100%;

    #videos {
        display: none;

        grid-template-rows: repeat(3, calc(33% - 1rem));
        //gap: 1rem;

        align-items: center;
        height: 100%;

        padding: 1rem;

        img {
            max-height: 90%;
            width: 100%;
        }
    }
}

#main_menu {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 0.5rem));
    grid-template-rows: repeat(2, calc(50% - 0.5rem));
    padding: 1rem;
    gap: 1rem;

    justify-content: center;

    .kor {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        text-align: center;

        &.logo img {
            max-width: 100%;
            max-height: 100%;
        }

        img {
            max-width: 60%;
            max-height: 60%;
        }

        .img_container {
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;

            aspect-ratio: 1;
            width: 70%;
        }

        &.zold .img_container {
            background-color: #b8dd74;
        }

        &.turkiz .img_container {
            background-color: #74dcc1;
        }

        &.lila .img_container {
            background-color: #d2a4ef;
        }
    }
}

#side_menu {
    display: grid;
    grid-template-columns: repeat(3, calc(33% - 0.5rem));
    grid-template-rows: 40% 40%;
    padding-top: 0.5rem;
    gap: 0.5rem;

    background-color: rgb(230, 217, 195);

    img {
        max-height: 100%;
        margin: 0 auto;
    }

    a {
        height: 100%;
        display: grid;
        grid-template-rows: 60% 40%;
        grid-template-columns: 100%;
        justify-content: center;
        align-items: center;

        text-decoration: none;
        color: white;
        
        p {
            margin: 0 auto;
            text-align: center;
            width: fit-content;
        } 
    }
}

#about_us {
    width: 95%;
    margin: 0 auto;
}

.fiftyfifty {
    grid-template-columns: 100%;
    justify-content: center;

    img {
        max-width: 100%;
        margin: 1rem auto;
    }
}

@media (orientation: landscape) {
    #header {
        .menu {
            display: flex;
        }

        height: 10vh;
    }

    .splash {
        grid-template-rows: 10% 90%;
    }

    #main_menu {
        /*grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 100%;*/
        display: block;
        position: relative;

        a {
            display: inline-block;
            position: absolute;
            width: fit-content;
        }

        a:nth-child(1) {
            margin: 0 auto;
            left: 0;
            right: 0;
            top: 40%;
        }

        a:nth-child(2) {
            left: 20%;
            top: 64%;
        }

        a:nth-child(3) {
            margin: 0 auto;
            left: 0;
            right: 0;
            top: 2.5%;
        }

        a:nth-child(4) {
            left: 62%;
            top: 64%;
        }

        :not(.logo) .img_container {
            width: 25vh;
            height: 25vh;

            max-width: 100%;
            max-height: 100%;
        }

        .logo .img_container {
            width: 27.5vh;
            height: 27.5vh;
        }
    }

    #main { 
        display: grid;
        grid-template-columns: 70% 30%;
        grid-template-rows: 100%;

        #videos {
            display: grid;
        }
    }

    #side_menu {
        display: none;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: 100%;
    }

    .fiftyfifty {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 100%;
        gap: 3rem;
        justify-content: center;

        img {
            margin: 0 auto;
        }
    }
}

/* --- */

body {
    &.zold {
        --color: #b8dd74;
    }

    &.turkiz {
        --color: #74dcc1;
    }

    &.lila {
        --color: #d2a4ef;
    }
}

#title {
    padding: 1rem;

    p {
        margin: 0 auto;
        width: fit-content;
        font-weight: 900;
        font-size: 2rem;
        padding: 0.5rem;
        text-align: center;
    }
}

#content {
    width: Min(95%, 1100px);
    margin: 0 auto;

    &>p {
        line-height: 1.5;
        hyphens: auto;
    }

    h3 {
        text-decoration: underline;
        text-decoration-thickness: 4px;
        text-decoration-color: var(--color, #666);
        width: fit-content;

        font-size: 1.5rem;
    }
}

.highlight {
    margin-top: 1rem;
    margin-bottom: 1rem;

    padding: 1rem;
    font-size: 1.5rem;
    background-color: var(--color, #666);
    color: white;

    p:first-child {
        margin-top: 0;
    }
    p:last-child {
        margin-bottom: 0;
    }
}

.white_highlight {
    margin-top: 1rem;
    margin-bottom: 1rem;

    padding: 1rem;
    background-color: white;
    color: #444;

    *:first-child {
        margin-top: 0;
    }

    *:last-child {
        margin-bottom: 0;
    }
}

table {
    border: 1px solid;

    tr td:first-child {
        font-weight: bold;
    }

    td {
        border: 1px solid;
        padding: 0.2rem;
    }

    tr:nth-child(even) {
        background-color: #efefef;
    }
}